<template>
  <v-app>
    <v-app-bar app :color="appTheme.primary" dark elevation="2">
      <v-container class="py-0 px-2 px-sm-6">
        <v-row align="center" no-gutters>
          <router-link to="/" class="text-decoration-none">
            <v-btn text class="app-nav-btn font-weight-medium">Home</v-btn>
          </router-link>
          <router-link to="/tools" class="text-decoration-none">
            <v-btn text class="app-nav-btn font-weight-medium">Tools</v-btn>
          </router-link>
          <router-link to="/blog" class="text-decoration-none">
            <v-btn text class="app-nav-btn font-weight-medium">Wiki</v-btn>
          </router-link>
          <router-link to="/about" class="text-decoration-none">
            <v-btn text class="app-nav-btn font-weight-medium">About</v-btn>
          </router-link>
          <v-spacer></v-spacer>
          <v-tooltip
            bottom
            v-if="userIsAuthenticated && config.authentication.required"
          >
            <template v-slot:activator="{ on }">
              <v-btn icon v-on="on" @click="logOut">
                <v-icon>mdi-logout</v-icon>
              </v-btn>
            </template>
            Log Out
          </v-tooltip>
        </v-row>
      </v-container>
    </v-app-bar>

    <v-main :class="{ 'gradient-background': !userIsAuthenticated }">
      <template v-if="!userIsAuthenticated && config.authentication.required">
        <v-container fill-height fluid>
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="6" lg="4" xl="3">
              <v-card rounded="lg" class="auth-card" elevation="8">
                <v-form @submit.prevent="doLogin" class="px-8 px-sm-10">
                  <v-card-text align="center" class="py-8">
                    <h1 class="text-h4 mb-6 font-weight-medium">Sign In</h1>
                    <v-text-field
                      v-model="userName"
                      label="Username"
                      prepend-inner-icon="mdi-account"
                      outlined
                      dense
                      hide-details="auto"
                      class="mb-4"
                      :color="appTheme.accent"
                    />
                    <v-text-field
                      v-model="userPass"
                      label="Password"
                      type="password"
                      prepend-inner-icon="mdi-lock"
                      outlined
                      dense
                      hide-details="auto"
                      class="mb-2"
                      :color="appTheme.accent"
                    />

                    <v-alert
                      v-if="showInvalidUser"
                      dense
                      outlined
                      type="error"
                      class="mt-4 mb-1 text-left"
                    >
                      <v-row align="center" no-gutters>
                        <span>Invalid username or password.</span>
                      </v-row>
                    </v-alert>

                    <v-row align="center" justify="center" class="mt-6">
                      <v-col cols="auto" class="pa-2">
                        <v-btn
                          type="submit"
                          :color="appTheme.primary"
                          dark
                          depressed
                          min-width="110"
                          @click="doLogin"
                          class="px-6"
                        >
                          <v-icon left>mdi-login</v-icon>
                          Sign In
                        </v-btn>
                      </v-col>
                      <v-col
                        cols="auto"
                        class="pa-2"
                        v-if="config.authentication.allowGuestLogin"
                      >
                        <v-btn
                          :color="appTheme.accent"
                          dark
                          depressed
                          min-width="110"
                          @click="doLoginAsAGuest"
                          class="px-6"
                        >
                          <v-icon left>mdi-account-outline</v-icon>
                          Guest
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-form>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </template>

      <template v-else>
        <v-container class="py-4 px-4 px-sm-6">
          <router-view />
        </v-container>
      </template>
    </v-main>

    <v-footer :color="appTheme.primary" dark app padless class="px-0">
      <v-card-text class="py-2 text-center">
        <div class="subtitle-2 pb-1">
          Copyright © {{ new Date().getFullYear() }} {{ config.appInfo.author }}
        </div>
      </v-card-text>
    </v-footer>
  </v-app>
</template>

<script>
import config from "./config.json";

export default {
  name: "App",

  components: {},

  data: () => ({
    config,
    // Color themes - choose one or customize
    themes: {
      deepBlue: {
        primary: "#183153", // Deep navy blue
        accent: "#3a7ca5", // Medium blue
        highlight: "#4e9eca",
      },
      modernDark: {
        primary: "#2c3e50", // Dark blue-gray
        accent: "#16a085", // Teal
        highlight: "#2ecc71",
      },
      elegantPurple: {
        primary: "#2e294e", // Deep purple
        accent: "#7b5e7b", // Muted purple
        highlight: "#d499b9",
      },
    },
    // Selected theme from config
    selectedTheme: "",
    // Original links - moved to About page
    icons: [
      {
        address: "https://github.com/co2storage",
        icon: "mdi-github",
        tooltip: "My Github Page",
      },
      {
        address: "https://www.linkedin.com/in/co2storage/",
        icon: "mdi-linkedin",
        tooltip: "My Linkedin Page",
      },
      {
        address: "mailto: tavagh.behnam@gmail.com",
        icon: "mdi-gmail",
        tooltip: "Send me email at tavagh.behnam@gmail.com",
      },
      {
        address: "tel:+4746212154",
        icon: "mdi-phone-classic",
        tooltip: "Call me at (+47) 46212154",
      },
    ],
    userName: "",
    userPass: "",
    userIsAuthenticated: !config.authentication.required,
    showInvalidUser: false,
  }),

  created() {
    // Set selected theme from config
    this.selectedTheme = config.appearance.defaultTheme;
  },

  computed: {
    appTheme() {
      return this.themes[this.selectedTheme];
    },
  },

  methods: {
    doLogin() {
      // Find user in the config's default users
      const validUser = this.config.authentication.defaultUsers.find(
        (user) =>
          user.username === this.userName && user.password === this.userPass
      );

      if (validUser) {
        this.userIsAuthenticated = true;
        this.showInvalidUser = false;
      } else {
        this.showInvalidUser = true;
      }
    },

    doLoginAsAGuest() {
      if (this.config.authentication.allowGuestLogin) {
        // Find guest user in the config
        const guestUser = this.config.authentication.defaultUsers.find(
          (user) => user.role === "viewer"
        );

        if (guestUser) {
          this.userName = guestUser.username;
          this.userPass = guestUser.password;
          this.userIsAuthenticated = true;
          this.showInvalidUser = false;
        }
      }
    },

    logOut() {
      if (this.config.authentication.required) {
        this.userIsAuthenticated = false;
        this.userName = "";
        this.userPass = "";
        this.showInvalidUser = false;
      }
    },
  },
};
</script>

