<template>
  <v-container class="converter-container pa-4 my-6">
    <v-card elevation="3" class="rounded-lg">
      <v-card-title
        class="text-center py-3"
        :style="{
          backgroundColor: $vuetify.theme.themes.light.primary,
          color: 'white',
        }"
      >
        <v-icon large left color="white">mdi-swap-horizontal</v-icon>
        Unit Converter
      </v-card-title>

      <v-card-text class="pa-4">
        <!-- Quantity Selection -->
        <v-select
          v-model="selected_quantity"
          :items="quantities"
          @change="onChange"
          label="Select quantity type"
          filled
          dense
          class="mb-4"
          :color="$vuetify.theme.themes.light.accent"
        ></v-select>

        <v-divider class="mb-4"></v-divider>

        <!-- Conversion Inputs -->
        <v-row align="center">
          <!-- From -->
          <v-col cols="12" sm="5">
            <v-text-field
              v-model="input_value"
              type="number"
              label="From"
              filled
              class="centered-input"
              hide-details
              dense
            >
              <template v-slot:append>
                <div v-html="fromUnitSymbol"></div>
              </template>
            </v-text-field>

            <v-select
              v-model="value_asked"
              :items="filteredUnits"
              item-text="completeName"
              item-value="name"
              filled
              label="Unit"
              dense
              class="mt-2 mb-5"
              hide-details
            ></v-select>
          </v-col>

          <!-- Swap button -->
          <v-col cols="12" sm="2" class="text-center">
            <v-btn
              icon
              :color="$vuetify.theme.themes.light.accent"
              @click="swapUnits"
            >
              <v-icon>mdi-swap-horizontal</v-icon>
            </v-btn>
          </v-col>

          <!-- To -->
          <v-col cols="12" sm="5">
            <v-text-field
              v-model="calculatedValue"
              ref="textToCopy"
              readonly
              label="To"
              filled
              class="centered-input"
              hide-details
              dense
            >
              <template v-slot:append>
                <div v-html="toUnitSymbol"></div>
              </template>
            </v-text-field>

            <v-select
              v-model="value_target"
              :items="filteredUnits"
              item-text="completeName"
              item-value="name"
              filled
              label="Unit"
              dense
              class="mt-2"
              hide-details
            ></v-select>
          </v-col>
        </v-row>

        <!-- <v-row class="mt-6">
          <v-col class="text-center">
            <v-btn
              :color="$vuetify.theme.themes.light.primary"
              @click="copyText"
              rounded
            >
              <v-icon left>mdi-content-copy</v-icon>
              Copy result
            </v-btn>
            <v-btn
              :color="$vuetify.theme.themes.light.accent"
              @click="resetValues"
              rounded
              class="ml-2"
            >
              <v-icon left>mdi-refresh</v-icon>
              Reset
            </v-btn>
          </v-col>
        </v-row> -->
      </v-card-text>

      <!-- Quick conversion reference -->
      <!-- <v-expand-transition>
        <div v-if="showReference">
