  methods: {
    copyText() {
      let textToCopy = this.$refs.textToCopy.$el.querySelector("input");
      textToCopy.select();
      document.execCommand("copy");

      // Add a visual feedback
      this.$nextTick(() => {
        this.showCopyFeedback = true;
        setTimeout(() => {
          this.showCopyFeedback = false;
        }, 2000);
      });
    },
    onChange() {
      const firstUnit = this.filteredUnits[0]?.name || "";
      const lastUnit =
        this.filteredUnits[this.filteredUnits.length - 1]?.name || "";
      this.value_asked = firstUnit;
      this.value_target = lastUnit !== firstUnit ? lastUnit : firstUnit;
    },
    swapUnits() {
      // Swap the units
      const temp = this.value_asked;
      this.value_asked = this.value_target;
      this.value_target = temp;

      // Set input value to the calculated value
      this.input_value = this.calculatedValue;
    },
    resetValues() {
      this.input_value = "1";
    },
  },
  mounted() {
    // Initialize with some sensible defaults
    this.onChange();
  },
  computed: {
    filteredUnits() {
      return this.items_input.filter(
        (item) => item.group === this.selected_quantity
      );
    },
    calculatedValue() {
      try {
        return convert(parseFloat(this.input_value) || 0)
          .from(this.value_asked)
          .to(this.value_target)
          .toFixed(6)
          .replace(/\.?0+$/, ""); // Remove trailing zeros
      } catch (error) {
        return "Error";
      }
    },
    fromUnitSymbol() {
      const unit = this.filteredUnits.find(
        (item) => item.name === this.value_asked
      );
      return unit ? unit.symbol : "";
    },
    toUnitSymbol() {
      const unit = this.filteredUnits.find(
        (item) => item.name === this.value_target
      );
      return unit ? unit.symbol : "";
    },
    referenceUnitName() {
      const unit = this.filteredUnits.find(
        (item) => item.name === this.value_asked
      );
      return unit ? unit.completeName : "";
    },
    targetUnitName() {
      const unit = this.filteredUnits.find(
        (item) => item.name === this.value_target
      );
      return unit ? unit.completeName : "";
    },
    referenceValue() {
      try {
        return convert(1)
          .from(this.value_asked)
          .to(this.value_target)
          .toFixed(6)
          .replace(/\.?0+$/, "");
      } catch (error) {
        return "Error";
      }
    },
  },
};
</script>

