<template>
  <div class="markdown-container">
    <!-- Left sidebar for navigation -->
    <aside class="left-sidebar">
      <h2>Wiki</h2>
      <div class="sidebar-categories">
        <!-- Dynamically generate categories from blog data -->
        <div
          v-for="categoryName in categoriesWithArticles"
          :key="categoryName"
          class="sidebar-category"
        >
          <div
            class="sidebar-category-header"
            @click="toggleCategory(categoryName)"
          >
            <span>{{
              categoryName.charAt(0).toUpperCase() + categoryName.slice(1)
            }}</span>
            <i
              class="v-icon mdi"
              :class="
                categoryOpen[categoryName]
                  ? 'mdi-chevron-down'
                  : 'mdi-chevron-right'
              "
            ></i>
          </div>
          <div
            class="sidebar-subcategories"
            v-show="categoryOpen[categoryName]"
          >
            <router-link
              v-for="article in categoryArticles[categoryName]"
              :key="article.id"
              :to="article.route"
              :class="{ 'active-article': currentArticleId === article.id }"
              class="article-link"
            >
              <span
                class="article-indicator"
                v-if="currentArticleId === article.id"
                >›</span
              >
              {{ article.title }}
            </router-link>
          </div>
        </div>
      </div>
    </aside>

    <!-- Main content -->
    <div class="markdown-content-wrapper">
      <h1 v-if="articleTitle" class="page-title">{{ articleTitle }}</h1>
      <div class="markdown-content" v-html="renderedContent"></div>
    </div>

    <!-- Right sidebar TOC -->
    <aside class="toc-sidebar" v-if="tableOfContents.length > 0">
      <div class="toc-header">
        <h2>Table of Contents</h2>
      </div>
      <div class="toc-scrollable">
        <ul>
          <li v-for="(item, index) in tableOfContents" :key="index">
            <a
              :href="'#' + item.slug"
              @click="handleTocClick(item.slug)"
              :class="{ active: activeHeadingId === item.slug }"
              :style="{ 'padding-left': item.level * 0.5 + 'rem' }"
            >
              {{ item.text }}
            </a>
          </li>
        </ul>
      </div>
    </aside>
  </div>
</template>

<script>
import MarkdownIt from "markdown-it";
import katex from "katex";
import "katex/dist/katex.min.css";
import { blogPosts, categoryColors, categoryOrder } from "@/blogData.js";

// Custom YouTube plugin for MarkdownIt
function youtubePlugin(md) {
  // Regular expression to match YouTube shortcodes
  const youtubeRegex = /\[\[youtube:([a-zA-Z0-9_-]+)\]\]/g;

  // Add a rule to parse YouTube shortcodes
  const defaultRender =
    md.renderer.rules.text ||
    function (tokens, idx, options, env, self) {
      return self.renderToken(tokens, idx, options, env, self);
    };

  md.renderer.rules.text = function (tokens, idx, options, env, self) {
    let content = tokens[idx].content;

    // Replace YouTube shortcodes with embed HTML
    if (content.match(youtubeRegex)) {
      content = content.replace(youtubeRegex, function (match, videoId) {
        return `<div class="youtube-embed-container">
          <iframe
            src="https://www.youtube.com/embed/${videoId}"
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen>
          </iframe>
        </div>`;
      });

      return content;
    }

    // If no match, use the default renderer
    return defaultRender(tokens, idx, options, env, self);
  };
}

export default {
  name: "MarkdownRenderer",
  props: {
    content: {
      type: String,
      required: true,
    },
    category: {
      type: String,
      default: "thermodynamics",
    },
    currentArticle: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      categoryColors: categoryColors,
      tableOfContents: [],
      activeHeadingId: "",
      observedHeadings: [],
      articleTitle: "",
      // Navigation data - initialize with an empty object
      categoryOpen: {},
      // Blog posts data
      blogPosts: blogPosts,
      // Category order
      categoryOrder: categoryOrder,
    };
  },
  computed: {
    renderedContent() {
      const md = new MarkdownIt({
        html: true,
        linkify: true,
        typographer: true,
        breaks: true,
      });

      md.use(youtubePlugin);
      // md.use(referencesPlugin);
      // Override the image renderer to handle spaces in filenames and add figure wrapper
      const defaultImageRenderer =
        md.renderer.rules.image ||
        function (tokens, idx, options, env, self) {
          return self.renderToken(tokens, idx, options);
        };

      md.renderer.rules.image = (tokens, idx, options, env, self) => {
        const token = tokens[idx];
        const srcIndex = token.attrIndex("src");
        const imgTitle = token.attrGet("title") || "";

        if (srcIndex >= 0) {
          // Use figcaption if a title is provided
          if (imgTitle) {
            const figureOpen = `<figure class="article-figure">`;
            const figureClose = `<figcaption>${imgTitle}</figcaption></figure>`;
            const img = defaultImageRenderer(tokens, idx, options, env, self);
            return `${figureOpen}${img}${figureClose}`;
          }
        }

        // Use the default renderer if no title
        return defaultImageRenderer(tokens, idx, options, env, self);
      };

      // Pre-process content to handle TikZ before any LaTeX processing
      let content = this.content || "";

      // Process TikZ content using explicit markers with optional caption
      const tikzRegex =
        /<!-- tikz(?:\s+caption="([^"]*)")? -->\s*([\s\S]*?)\s*<!-- \/tikz -->/g;
      const tikzReplacements = [];

      content = content.replace(tikzRegex, (match, caption, tikzCode) => {
        // Encode the tikz code for URL
        const encodedTikz = encodeURIComponent(tikzCode);

        // Create URL for the upmath.me service
        const tikzUrl = `https://i.upmath.me/svg/${encodedTikz}`;

        // Create a placeholder ID
        const placeholderId = `TIKZ_PLACEHOLDER_${tikzReplacements.length}`;

        // Prepare HTML with optional caption
        let html = `<div class="tikz-diagram">
      <img src="${tikzUrl}" alt="TikZ Diagram" class="tikz-image">`;

        // Add caption if provided
        if (caption) {
          html += `<div class="tikz-caption">${caption}</div>`;
        }

        html += `</div>`;

        // Store the HTML for later replacement
        tikzReplacements.push(html);

        // Return a placeholder that won't be processed by math renderer
        return placeholderId;
      });

      // Process block math with auto-numbering
      const processBlockMath = (markdown) => {
        let result = markdown;
        const blockRegex = /\$\$([\s\S]+?)\$\$/g;

        // Use a local counter for equation numbering
        let localCounter = 0;

        result = result.replace(blockRegex, (match, latex) => {
          try {
            // Check if there's already a \tag in the LaTeX
            const hasTag = latex.includes("\\tag{");

            // If no tag exists, increment counter for auto-numbering
            let equationNumber = "";
            let renderedLatex = latex;

            if (!hasTag) {
              localCounter++;
              equationNumber = localCounter.toString();
            } else {
              // Extract existing tag number
              const tagMatch = latex.match(/\\tag\{([^}]+)\}/);
              if (tagMatch) {
                equationNumber = tagMatch[1];
                renderedLatex = latex.replace(/\\tag\{([^}]+)\}/, "");
              }
            }

            // Render the equation
            const katexHtml = katex.renderToString(renderedLatex.trim(), {
              displayMode: true,
              throwOnError: false,
            });

            // Return the equation with the number positioned properly
            return `<div class="math-block">
                ${katexHtml}
                <span class="equation-number">(${equationNumber})</span>
              </div>`;
          } catch (error) {
            return `<div class="math-error">LaTeX Error: ${error.message}</div>`;
          }
        });

        return result;
      };

      // Process inline math delimited by single $
      const processInlineMath = (markdown) => {
        let result = markdown;
        // Updated regex to also match single character math expressions
        const inlineRegex =
          /(?<!\\\$)\$(?!\$)([^\s$]|[^\s$][^$]*?[^\s$])(?<!\\\$)\$(?!\$)/g;

        result = result.replace(inlineRegex, (match, latex) => {
          try {
            return katex.renderToString(latex.trim(), {
              displayMode: false,
              throwOnError: false,
            });
          } catch (error) {
            return `<span class="math-error">LaTeX Error: ${error.message}</span>`;
          }
        });

        return result;
      };

      // Process content in this order: block math, inline math
      content = processBlockMath(content);
      content = processInlineMath(content);

      // Extract TOC data before rendering
      this.extractTableOfContents(content);

      // Remove any existing TOC markers or headings since we're using the sidebar
      content = content.replace(/\[TOC\]/g, "");
      content = content.replace(
        /^## Table of Contents[\s\S]*?<\/div>\n\n/m,
        ""
      );

      // Render markdown
      let rendered = md.render(content);

      // Put TikZ diagrams back in
      tikzReplacements.forEach((diagram, index) => {
        rendered = rendered.replace(`TIKZ_PLACEHOLDER_${index}`, diagram);
      });

      return rendered;
    },

    categoryColor() {
      return (
        this.categoryColors[this.category] || this.categoryColors.thermodynamics
      );
    },

    // Get current article ID from route
    currentArticleId() {
      return this.getArticleIdFromRoute(this.$route?.path || "");
    },

    // Computed property that filters categories to only include those with articles
    categoriesWithArticles() {
      // First get all categories that have articles
      const categoriesWithContent = Object.keys(this.categoryColors).filter(
        (categoryName) =>
          this.categoryArticles[categoryName] &&
          this.categoryArticles[categoryName].length > 0
      );

      // Now sort them according to categoryOrder
      return categoriesWithContent.sort((a, b) => {
        const indexA = this.categoryOrder.indexOf(a);
        const indexB = this.categoryOrder.indexOf(b);

        // If category is not in the order array, put it at the end
        if (indexA === -1) return 1;
        if (indexB === -1) return -1;

        // Otherwise sort by the index in the order array
        return indexA - indexB;
      });
    },

    // Compute articles organized by category from blog posts
    categoryArticles() {
      const articles = {};

      // Initialize empty arrays for each category
      Object.keys(this.categoryColors).forEach((category) => {
        articles[category] = [];
      });

      // Populate articles by category
      this.blogPosts.forEach((post) => {
        const articleInfo = {
          id: this.getArticleIdFromRoute(post.route),
          title: post.title,
          route: post.route,
          subtitle: post.subtitle,
          icon: post.icon,
        };

        if (articles[post.category]) {
          articles[post.category].push(articleInfo);
        }
      });

      return articles;
    },
  },
  mounted() {
    this.$nextTick(() => {
      // Initialize categoryOpen object dynamically from all available categories
      Object.keys(this.categoryColors).forEach((category) => {
        this.$set(this.categoryOpen, category, false);
      });

      this.enhanceDOM();
      this.setupIntersectionObserver();
      this.adjustTocHeight();
      this.setupReferences();

      // this.setupPrinting();
      // Adjust TOC height on window resize
      window.addEventListener("resize", this.adjustTocHeight);

      // Check current route and open the appropriate category
      if (this.$route && this.$route.path) {
        const currentPostCategory = this.getCategoryFromRoute(this.$route.path);
        if (currentPostCategory) {
          this.openCategory(currentPostCategory);
        }
      }
    });
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.adjustTocHeight);
  },
  methods: {
    // Get article ID from route path
    getArticleIdFromRoute(route) {
      // Extract the last part of the route as the ID
      const parts = route.split("/");
      return parts[parts.length - 1];
    },

    // Get category from route
    getCategoryFromRoute(route) {
      const articleId = this.getArticleIdFromRoute(route);
      const post = this.blogPosts.find(
        (p) => this.getArticleIdFromRoute(p.route) === articleId
      );
      return post ? post.category : null;
    },

    // Open a category and close others
    openCategory(categoryToOpen) {
      // Reset all categories to closed
      Object.keys(this.categoryOpen).forEach((category) => {
        this.categoryOpen[category] = false;
      });

      // Open the specified category
      if (
        categoryToOpen &&
        Object.prototype.hasOwnProperty.call(this.categoryOpen, categoryToOpen)
      ) {
        this.categoryOpen[categoryToOpen] = true;
      }
    },

    // Toggle category expansion
    toggleCategory(category) {
      this.categoryOpen[category] = !this.categoryOpen[category];
    },

    extractTableOfContents(content) {
      // Reset TOC data
      this.tableOfContents = [];

      // Extract title (h1) if present
      const titleMatch = content.match(/^#\s+(.+)$/m);
      if (titleMatch) {
        this.articleTitle = titleMatch[1].trim();
      }

      // Find all headings using regex (h2, h3, h4)
      const headingRegex = /^(#{2,4})\s+(.+)$/gm;
      let match;

      while ((match = headingRegex.exec(content)) !== null) {
        const level = match[1].length - 1; // Get heading level (2-4)
        const text = match[2].trim();
        const slug = this.slugify(text);

        this.tableOfContents.push({
          level,
          text,
          slug,
        });
      }
    },

    handleTocClick(slug) {
      // Update active heading manually when clicked
      this.activeHeadingId = slug;
    },

    adjustTocHeight() {
      // Calculate the correct height for the scrollable area
      const tocSidebar = this.$el.querySelector(".toc-sidebar");
      const tocHeader = this.$el.querySelector(".toc-header");
      const tocScrollable = this.$el.querySelector(".toc-scrollable");

      if (tocSidebar && tocHeader && tocScrollable) {
        const headerHeight = tocHeader.offsetHeight;
        tocScrollable.style.height = `calc(100vh - ${headerHeight}px - 20px)`;
      }
    },

    slugify(text) {
      return text
        .toLowerCase()
        .replace(/[^\w\s-]/g, "") // Remove special chars
        .replace(/\s+/g, "-") // Replace spaces with hyphens
        .replace(/-+/g, "-"); // Remove consecutive hyphens
    },

    enhanceDOM() {
      // Process images and identify captions that immediately follow images
      const allParagraphs = this.$el.querySelectorAll("p");

      for (let i = 0; i < allParagraphs.length; i++) {
        const paragraph = allParagraphs[i];

        // Check if this paragraph contains only an image
        const images = paragraph.querySelectorAll("img");
        if (images.length === 1 && paragraph.childNodes.length === 1) {
          // This is an image paragraph
          paragraph.classList.add("image-container");

          // Check if the next paragraph contains only italic text (potential caption)
          const nextParagraph = allParagraphs[i + 1];
          if (
            nextParagraph &&
            nextParagraph.firstElementChild &&
            nextParagraph.firstElementChild.tagName === "EM" &&
            nextParagraph.childNodes.length === 1
          ) {
            // This is a caption paragraph
            nextParagraph.classList.add("caption");
            nextParagraph.classList.add("text-center");
          }
        }
      }

      // Add SVG-specific styling
      const svgImages = this.$el.querySelectorAll('img[src$=".svg"]');
      svgImages.forEach((img) => {
        const container = document.createElement("div");
        container.className = "svg-blend-container";
        img.parentNode.insertBefore(container, img);
        container.appendChild(img);
      });

      // Add responsive table containers
      const tables = this.$el.querySelectorAll("table");
      tables.forEach((table) => {
        const wrapper = document.createElement("div");
        wrapper.className = "table-responsive";
        table.parentNode.insertBefore(wrapper, table);
        wrapper.appendChild(table);
      });

      // Add heading anchors and IDs
      const headings = this.$el.querySelectorAll("h2, h3, h4");
      headings.forEach((heading) => {
        if (!heading.id) {
          heading.id = this.slugify(heading.textContent);
        }

        // Add anchor link icon
        const anchor = document.createElement("a");
        anchor.className = "heading-anchor";
        anchor.innerHTML = '<i class="v-icon mdi mdi-link-variant"></i>';
        anchor.href = `#${heading.id}`;
        heading.appendChild(anchor);

        // Add a target for the intersection observer
        const target = document.createElement("div");
        target.className = "toc-observer-target";
        target.id = `observer-${heading.id}`;
        heading.parentNode.insertBefore(target, heading);

        // Store the heading ID for observation
        this.observedHeadings.push(heading.id);
      });

      // Add reading progress indicator
      this.setupReadingProgress();
    },

    setupIntersectionObserver() {
      // Set up intersection observer for TOC highlighting
      if ("IntersectionObserver" in window) {
        const observerOptions = {
          rootMargin: "-100px 0px -80% 0px", // Adjust these values as needed
          threshold: 0,
        };

        const observer = new IntersectionObserver((entries) => {
          entries.forEach((entry) => {
            // The id format is "observer-heading-id"
            const headingId = entry.target.id.replace("observer-", "");

            if (entry.isIntersecting) {
              this.activeHeadingId = headingId;
            }
          });
        }, observerOptions);

        // Observe all heading targets
        this.observedHeadings.forEach((id) => {
          const target = document.getElementById(`observer-${id}`);
          if (target) {
            observer.observe(target);
          }
        });
      }
    },

    setupReadingProgress() {
      if (!document.querySelector(".reading-progress-container")) {
        // Create progress container
        const progressContainer = document.createElement("div");
        progressContainer.className = "reading-progress-container";

        // Create the progress bar
        const progressBar = document.createElement("div");
        progressBar.className = "reading-progress-bar";
        progressBar.style.backgroundColor = this.categoryColor;

        // Append to container
        progressContainer.appendChild(progressBar);
        document.body.appendChild(progressContainer);

        // Update progress on scroll
        const updateProgress = () => {
          const scrollTop =
            document.documentElement.scrollTop || document.body.scrollTop;
          const scrollHeight =
            document.documentElement.scrollHeight -
            document.documentElement.clientHeight;
          const scrollPercentage = (scrollTop / scrollHeight) * 100;
          progressBar.style.width = `${scrollPercentage}%`;
        };

        window.addEventListener("scroll", updateProgress);

        // Remove on component destroy
        this.$once("hook:beforeDestroy", () => {
          window.removeEventListener("scroll", updateProgress);
          if (progressContainer.parentNode) {
            progressContainer.parentNode.removeChild(progressContainer);
          }
        });
      }
    },
    formatCategoryName(categoryName) {
      // Split by spaces and capitalize each word
      return categoryName
        .split(" ")
        .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
        .join(" ");
    },
    setupReferences() {
      this.$nextTick(() => {
        const content = this.$el.querySelector(".markdown-content");
        if (!content) return;

        // Generate unique ID to avoid collisions if called multiple times
        this.refInstanceId = "ref-" + Date.now();

        // First pass: process citations in text to make them clickable
        this.processCitations(content);

        // Second pass: process reference list
        this.processReferenceList(content);
      });
    },

    // Process citation links in the text
    processCitations(content) {
      // Find citation patterns that might appear in the text
      // This matches:
      // - Standalone [1] or [^1]
      // - Citations after words like "as[1]" without a space
      const patterns = [
        /\s\[\^?(\d+)\]/g, // [1] or [^1] with space before
        /\[\^?(\d+)\]/g, // [1] or [^1] anywhere
        /as\[\^?(\d+)\]/gi, // specifically "as[1]" pattern
        /\(\[\^?(\d+)\]\)/g, // ([1]) pattern
      ];

      const walker = document.createTreeWalker(
        content,
        NodeFilter.SHOW_TEXT,
        null,
        false
      );

      const processedIds = new Set(); // Track processed ids to avoid duplicates

      // First collect all text nodes
      const nodesToProcess = [];
      let textNode;

      while ((textNode = walker.nextNode())) {
        let shouldProcess = false;
        for (const pattern of patterns) {
          if (pattern.test(textNode.nodeValue)) {
            shouldProcess = true;
            break;
          }
        }

        if (shouldProcess) {
          nodesToProcess.push(textNode);
        }
      }

      // Process each node for citations
      nodesToProcess.forEach((node) => {
        let newContent = node.nodeValue;

        // Process with each pattern
        for (const pattern of patterns) {
          newContent = this.processTextWithPattern(
            newContent,
            pattern,
            processedIds
          );
        }

        // If content changed, replace node
        if (newContent !== node.nodeValue) {
          // Use temporary div to convert HTML string to nodes
          const tempDiv = document.createElement("div");
          tempDiv.innerHTML = newContent;

          // Build a document fragment
          const fragment = document.createDocumentFragment();
          while (tempDiv.firstChild) {
            fragment.appendChild(tempDiv.firstChild);
          }

          node.parentNode.replaceChild(fragment, node);
        }
      });
    },

    // Process text with a specific citation pattern
    processTextWithPattern(text, pattern, processedIds) {
      const parts = [];
      let lastIndex = 0;
      let match;

      // Reset pattern for this text
      pattern.lastIndex = 0;

      while ((match = pattern.exec(text)) !== null) {
        // Get the full match and the number group
        const fullMatch = match[0];
        const id = match[1];
        const uniqueId = `citation-${id}-${this.refInstanceId}`;

        // Skip if already processed to prevent double-processing
        if (processedIds.has(uniqueId)) continue;
        processedIds.add(uniqueId);

        // Build link HTML
        const linkHtml = `<a href="#ref-${id}" class="citation-link">[${id}]</a>`;

        // Add text before match
        parts.push(text.substring(lastIndex, match.index));

        // Add modified citation
        // Handle special case for "as[1]" pattern
        if (/as\[\^?(\d+)\]/i.test(fullMatch)) {
          parts.push(`as${linkHtml}`);
        }
        // Handle ([1]) pattern
        else if (/\(\[\^?(\d+)\]\)/.test(fullMatch)) {
          parts.push(`(${linkHtml})`);
        }
        // Regular pattern with surrounding space preserved
        else {
          const prefix = /^\s/.test(fullMatch) ? " " : "";
          parts.push(prefix + linkHtml);
        }

        lastIndex = match.index + fullMatch.length;
      }

      // Add remaining text
      if (lastIndex < text.length) {
        parts.push(text.substring(lastIndex));
      }

      return parts.join("");
    },

    // Process reference list
    processReferenceList(content) {
      // Find references section
      let referencesSection = null;
      const headings = content.querySelectorAll("h1, h2, h3");

      for (const heading of headings) {
        if (heading.textContent.toLowerCase().includes("reference")) {
          referencesSection = heading;
          break;
        }
      }

      if (!referencesSection) return;

      // Find all paragraphs after the references heading
      let currentElement = referencesSection.nextElementSibling;
      const referenceItems = [];

      // Skip until we find paragraphs
      while (
        currentElement &&
        currentElement.tagName !== "H1" &&
        currentElement.tagName !== "H2" &&
        currentElement.tagName !== "H3"
      ) {
        // Look for paragraphs that contain references
        if (currentElement.tagName === "P") {
          const text = currentElement.textContent.trim();

          // Try to match reference patterns
          // First try strict pattern with punctuation
          let match = text.match(/^\[\^?(\d+)\][:.]\s+(.+)$/);

          // If no match, try more relaxed pattern without punctuation
          if (!match) {
            match = text.match(/^\[\^?(\d+)\]\s+(.+)$/);
          }

          // If still no match, try to extract the text after the reference number
          if (!match && text.startsWith("[")) {
            const bracketMatch = text.match(/^\[\^?(\d+)\]/);
            if (bracketMatch) {
              const id = bracketMatch[1];
              const remaining = text.substring(bracketMatch[0].length).trim();
              if (remaining.length > 0) {
                match = [null, id, remaining];
              }
            }
          }

          if (match) {
            const id = match[1];
            const refText = match[2];

            // Create styled reference item
            const refItem = document.createElement("div");
            refItem.className = "reference-item";
            refItem.id = `ref-${id}`;

            // Number part
            const numSpan = document.createElement("span");
            numSpan.className = "reference-number";
            numSpan.textContent = `[${id}]`;

            // Text part
            const textSpan = document.createElement("span");
            textSpan.className = "reference-text";
            textSpan.textContent = refText;

            refItem.appendChild(numSpan);
            refItem.appendChild(textSpan);

            referenceItems.push({
              original: currentElement,
              new: refItem,
            });
          }
        }

        currentElement = currentElement.nextElementSibling;
      }

      // Create references container if we found any
      if (referenceItems.length > 0) {
        const refsContainer = document.createElement("div");
        refsContainer.className = "references";

        // Add all reference items
        referenceItems.forEach((item) => {
          refsContainer.appendChild(item.new);
          item.original.style.display = "none";
        });

        // Insert right after the References heading
        referencesSection.after(refsContainer);
      }
    },
    setupPrinting() {
      // Create print button
      this.addPrintButton();

      // Add print CSS
      this.addPrintStyles();
    },

    // Add this to your methods section
    addPrintButton() {
      // Get the content wrapper element
      const contentWrapper = this.$el.querySelector(
        ".markdown-content-wrapper"
      );
      if (!contentWrapper) return;

      // Create button element
      const printButton = document.createElement("button");
      printButton.className = "print-button";
      printButton.innerHTML = `
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
      <polyline points="6 9 6 2 18 2 18 9"></polyline>
      <path d="M6 18H4a2 2 0 0 1-2-2v-5a2 2 0 0 1 2-2h16a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2h-2"></path>
      <rect x="6" y="14" width="12" height="8"></rect>
    </svg>
    Print
  `;

      // Add button styles
      const style = document.createElement("style");
      style.textContent = `
    .print-button {
      position: absolute;
      top: 1rem;
      right: 1rem;
      background-color: #f8f9fa;
      border: 1px solid #e1e4e8;
      border-radius: 4px;
      padding: 0.5rem 1rem;
      font-size: 0.9rem;
      cursor: pointer;
      display: flex;
      align-items: center;
      color: #24292e;
      transition: background-color 0.2s;
      z-index: 100;
    }
    
    .print-button:hover {
      background-color: #e1e4e8;
    }
    
    .print-button svg {
      margin-right: 0.5rem;
      width: 16px;
      height: 16px;
    }
    
    @media print {
      .print-button {
        display: none;
      }
      
      /* Hide navigation elements */
      .left-sidebar, .toc-sidebar, .reading-progress-container {
        display: none;
      }
      
      /* Just hide the container itself, not the content */
      .markdown-container {
        display: block;
        width: 100%;
      }
      
      /* Make content full width */
      .markdown-content-wrapper {
        width: 100%;
        border: none;
        padding: 0;
        margin: 0;
      }
      
      /* Add site footer */
      .print-footer {
        text-align: center;
        font-size: 10pt;
        color: #666;
        margin-top: 2em;
        border-top: 1px solid #eee;
        padding-top: 1em;
      }
    }
  `;
      document.head.appendChild(style);

      // Make wrapper position relative for proper button positioning
      contentWrapper.style.position = "relative";

      // Add click event listener
      printButton.addEventListener("click", () => {
        // Save original title
        const originalTitle = document.title;

        // Set title to article title if available
        if (this.articleTitle) {
          document.title = this.articleTitle;
        }

        // Add footer with website
        const footer = document.createElement("div");
        footer.className = "print-footer";
        footer.innerHTML = "<strong>co2storage.github.io</strong>";
        contentWrapper.appendChild(footer);

        // Print
        window.print();

        // Restore original title and remove footer
        setTimeout(() => {
          document.title = originalTitle;
          contentWrapper.removeChild(footer);
        }, 100);
      });

      // Add button to DOM
      contentWrapper.appendChild(printButton);
    },
  },
  watch: {
    // Update when route changes
    $route(to) {
      // Find the category for the current route
      const currentPostCategory = this.getCategoryFromRoute(to.path);

      // Open the relevant category and close others
      this.openCategory(currentPostCategory);
    },
  },
};
</script>

<style>
/* Base structure with properly centered content */
.markdown-container {
  display: grid;
  grid-template-columns: 220px minmax(700px, 900px) 220px;
  /* max-width: 1800px; */
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  gap: 20px;
  justify-content: center;
}

/* Clean body setup */
body {
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #202122;
}

/* Navbar positioning */
nav,
.navbar,
header {
  z-index: 100;
  position: sticky;
  top: 0;
  background-color: #00274c;
  color: white;
  width: 100%;
}

/* Left sidebar styles */
.left-sidebar {
  grid-column: 1;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid #e1e4e8;
  padding: 0;
  position: sticky;
  top: 80px;
  height: fit-content;
  align-self: start;
  overflow: hidden;
}

.left-sidebar h2 {
  font-size: 1rem;
  padding: 0.8rem 1rem;
  margin: 0;
  color: #24292e;
  font-weight: 600;
  background-color: #f6f8fa;
  border-bottom: 1px solid #e1e4e8;
  text-align: left;
}

.sidebar-categories {
  display: flex;
  flex-direction: column;
}

.sidebar-category {
  border-bottom: 1px solid #e9ecef;
}

.sidebar-category:last-child {
  border-bottom: none;
}

.sidebar-category-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
  font-weight: 500;
  color: #24292e;
}

.sidebar-category-header:hover {
  background-color: #f6f8fa;
}

.sidebar-category-header.active-category {
  background-color: #f0f4f8;
  color: #0645ad;
  border-left: 3px solid #0645ad;
  padding-left: calc(1rem - 3px);
}

.sidebar-subcategories {
  display: flex;
  flex-direction: column;
  background-color: #f8f9fa;
  padding: 0.5rem 0;
}

.sidebar-subcategories .article-link {
  color: #0645ad;
  text-decoration: none;
  display: block;
  padding: 0.4rem 1rem 0.4rem 2rem;
  font-size: 0.9rem;
  transition: background-color 0.1s ease;
  position: relative;
}

.sidebar-subcategories .article-link:hover {
  background-color: #f0f4f8;
  text-decoration: none;
}

.sidebar-subcategories .article-link .article-indicator {
  position: absolute;
  left: 1.3rem;
  color: #0645ad;
  font-weight: bold;
  font-size: 1rem;
}

/* TOC sidebar on right */
.toc-sidebar {
  grid-column: 3;
  position: sticky;
  top: 80px;
  height: fit-content;
  max-height: calc(100vh - 100px);
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid #e1e4e8;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  align-self: start;
  font-size: 0.9rem;
}

.toc-header {
  padding: 0.8rem 1rem;
  border-bottom: 1px solid #e1e4e8;
  background-color: #f6f8fa;
}

.toc-header h1.article-title {
  font-size: 1.1rem;
  margin: 0 0 0.8rem 0;
  color: #24292e;
  line-height: 1.3;
  font-weight: 600;
}

.toc-header h2 {
  font-size: 1rem;
  margin: 0;
  color: #24292e;
  font-weight: 600;
}

.toc-scrollable {
  flex: 1;
  overflow-y: auto;
  padding: 0.5rem 0;
  max-height: calc(100vh - 160px);
}

.toc-scrollable::-webkit-scrollbar {
  width: 4px;
}

.toc-scrollable::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.toc-scrollable::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 4px;
}

.toc-sidebar ul {
  padding: 0;
  list-style-type: none;
  margin: 0;
}

.toc-sidebar ul li {
  margin: 0;
  padding: 0;
}

.toc-sidebar ul li a {
  color: #0645ad;
  text-decoration: none;
  display: block;
  padding: 0.4rem 1rem;
  border-left: 2px solid transparent;
  transition: background-color 0.1s ease;
}

.toc-sidebar ul li a:hover {
  background-color: #f6f8fa;
  text-decoration: underline;
}

.toc-sidebar ul li a.active {
  border-left: 2px solid #0645ad;
  background-color: #eaecf0;
  font-weight: 500;
}

/* Properly centered content wrapper */
.markdown-content-wrapper {
  grid-column: 2;
  width: 100%;
  /* width: 800px; */
  background-color: white;
  border-radius: 4px;
  border: 1px solid #e1e4e8;
  overflow: hidden;
  padding: 0;
}

.page-title {
  text-align: center;
  padding: 1.5rem 1rem 1rem;
  margin: 0 0 1.5rem 0;
  color: #24292e;
  font-size: 1.8rem;
  font-weight: 600;
  border-bottom: 1px solid #e1e4e8;
}

.markdown-content {
  font-size: 1.2rem;
  line-height: 1.6;
  color: #24292e;
  padding: 0 2rem 2rem;
}

/* Clean heading styles */
.markdown-content h1 {
  font-size: 1.8rem;
  margin: 1.5rem 0 1rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #e1e4e8;
  font-weight: 600;
  color: #24292e;
}

.markdown-content h2 {
  font-size: 1.5rem;
  margin: 1.5rem 0 1rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #e1e4e8;
  font-weight: 600;
  color: #24292e;
}

.markdown-content h3 {
  font-size: 1.25rem;
  margin: 1.5rem 0 1rem;
  font-weight: 600;
  color: #24292e;
}

.markdown-content h4 {
  font-size: 1.1rem;
  margin: 1.5rem 0 1rem;
  font-weight: 600;
  color: #24292e;
}

/* Subtle heading anchors */
.heading-anchor {
  margin-left: 8px;
  opacity: 0;
  font-size: 0.8em;
  color: #0645ad;
}

.markdown-content h2:hover .heading-anchor,
.markdown-content h3:hover .heading-anchor,
.markdown-content h4:hover .heading-anchor {
  opacity: 1;
}

/* Clean text styles */
.markdown-content p {
  margin-bottom: 1rem;
}

.markdown-content strong {
  font-weight: 600;
}

.markdown-content em {
  font-style: italic;
}

/* Improved list styling */
.markdown-content ul,
.markdown-content ol {
  margin-bottom: 1rem;
  padding-left: 2rem;
}

.markdown-content li {
  margin-bottom: 0.5rem;
}

.markdown-content li p {
  margin-top: 0.3rem;
  margin-bottom: 0.3rem;
}

/* Math blocks with proper centering */
.math-block {
  position: relative;
  overflow-x: auto;
  margin: 0 0;
  display: flex;
  justify-content: center;
}

.equation-number {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.1rem;
  color: #54595d;
}

/* Image and figure styles */
.article-figure {
  margin: 0 0;
  text-align: center;
}

.article-figure figcaption {
  color: #54595d;
  margin-top: 0.5rem;
  font-style: italic;
  font-size: 1.1rem;
}

.image-container {
  display: flex;
  justify-content: center;
  margin: 1.5rem 0;
}

.caption {
  font-size: 1.1rem;
  color: #54595d;
  margin-top: 0.5rem;
  text-align: center;
  font-style: italic;
}

.markdown-content img {
  /* max-width: 100%; */
  width: 600px;
  height: auto;
  display: block;
  margin: 0rem auto;
}

/* Table styling */
.table-responsive {
  overflow-x: auto;
  margin: 1.5rem 0;
  border: 1px solid #e1e4e8;
  border-radius: 3px;
}

.markdown-content table {
  border-collapse: collapse;
  width: 100%;
  margin: 0;
}

.markdown-content table th,
.markdown-content table td {
  border: 1px solid #e1e4e8;
  padding: 0.5rem 0.8rem;
}

.markdown-content table th {
  background-color: #f6f8fa;
  font-weight: 600;
  text-align: left;
}

.markdown-content table tr:nth-child(even) {
  background-color: #f8f9fa;
}

/* Code styling */
.markdown-content pre {
  background-color: #f6f8fa;
  border: 1px solid #e1e4e8;
  border-radius: 3px;
  padding: 0.8rem;
  overflow-x: auto;
  margin: 1rem 0;
  font-size: 0.9rem;
}

.markdown-content code {
  font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 0.9rem;
  background-color: #f6f8fa;
  padding: 0.2rem 0.4rem;
  border-radius: 3px;
  border: 1px solid #e1e4e8;
}

.markdown-content pre code {
  padding: 0;
  background-color: transparent;
  border: none;
}

/* Blockquote styling */
.markdown-content blockquote {
  border-left: 3px solid #c8ccd1;
  padding: 0.5rem 1rem;
  margin: 1rem 0;
  background-color: #f8f9fa;
  color: #54595d;
}

/* Subtle reading progress bar */
.reading-progress-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  z-index: 9999;
}

.reading-progress-bar {
  height: 100%;
  width: 0;
  transition: width 0.1s ease;
}

/* SVG container for blending */
.svg-blend-container {
  display: flex;
  justify-content: center;
  margin: 0.5rem 0;
}

/* Improved responsive design with proper centering */
@media (max-width: 1200px) {
  .markdown-container {
    grid-template-columns: 200px minmax(500px, 700px) 200px;
    padding: 0 15px;
  }
}

@media (max-width: 1024px) {
  .markdown-container {
    grid-template-columns: 180px 1fr 180px;
    padding: 0 10px;
    gap: 15px;
  }

  .markdown-content {
    padding: 0 1.5rem 1.5rem;
  }
}

@media (max-width: 900px) {
  .markdown-container {
    grid-template-columns: 1fr;
    grid-template-areas:
      "content"
      "toc"
      "left";
    padding: 10px;
  }

  .markdown-content-wrapper {
    grid-area: content;
  }

  .toc-sidebar {
    grid-area: toc;
    position: relative;
    top: 0;
    height: auto;
    margin-top: 15px;
  }

  .left-sidebar {
    grid-area: left;
    position: relative;
    top: 0;
    margin-top: 15px;
  }

  .toc-scrollable {
    max-height: 300px;
  }
}

@media (max-width: 600px) {
  .markdown-container {
    padding: 8px;
  }

  .markdown-content {
    padding: 0 1rem 1.5rem;
  }

  .page-title {
    font-size: 1.5rem;
    padding: 1rem 0.5rem;
  }

  .markdown-content h1 {
    font-size: 1.5rem;
  }

  .markdown-content h2 {
    font-size: 1.3rem;
  }

  .markdown-content h3 {
    font-size: 1.1rem;
  }
}

/* Handle case with no TOC */
.markdown-container:not(:has(.toc-sidebar)) {
  grid-template-columns: 220px minmax(600px, 800px);
}

@media (max-width: 900px) {
  .markdown-container:not(:has(.toc-sidebar)) {
    grid-template-columns: 1fr;
    grid-template-areas:
      "content"
      "left";
  }
}

/* YouTube embed container */
.youtube-embed-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
  overflow: hidden;
  max-width: 100%;
  margin: 1.5rem 0;
  border: 1px solid #e1e4e8;
  border-radius: 4px;
  background-color: #f6f8fa; /* Light background until video loads */
}

.youtube-embed-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

/* TikZ diagram styles */
.tikz-diagram {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1.5rem 0;
  overflow-x: auto;
}

.tikz-image {
  max-width: 100%;

  height: auto;
}

.tikz-caption {
  font-size: 0.9rem;
  color: #54595d;
  margin-top: 0.5rem;
  text-align: center;
  font-style: italic;
}

/* Reference list items */
.reference-item {
  font-size: 0.95rem;
  color: #666;
  margin-bottom: 0.8rem;
  text-align: left;
  display: flex;
  align-items: flex-start;
}

/* Reference number styling - NOT italic */
.reference-number {
  margin-right: 0.5rem;
  color: #666;
  font-style: normal; /* Changed from italic to normal */
  font-weight: normal;
}

/* Reference text styling - remains italic */
.reference-text {
  flex: 1;
  font-style: italic;
}

.references {
  margin: 1.5rem 0;
}

.citation-link {
  color: #0645ad;
  text-decoration: none;
  font-size: 0.85em;
  font-weight: normal;
}

.citation-link:hover {
  text-decoration: underline;
}
</style>
