import Vue from "vue";
import VueRouter from "vue-router";
import HomePage from "../views/HomePage.vue";
import AboutMe from "../views/AboutMe.vue";
import ToolsPage from "../views/ToolsPage.vue";
import BlogPage from "../views/BlogPage.vue";
import UnitConverter from "../views/UnitConverter.vue";
import TwoPhaseFlash from "../views/TwoPhaseFlash.vue";
import BlogPost from "../components/BlogPost.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "HomePage",
    component: HomePage,
  },
  {
    path: "/about",
    name: "AboutPage",
    component: AboutMe,
  },
  {
    path: "/tools",
    name: "ToolsPage",
    component: ToolsPage,
  },
  {
    path: "/blog",
    name: "BlogPage",
    component: BlogPage,
  },
  {
    path: "/tools/unit-converter",
    name: "UnitConverter",
    component: UnitConverter,
  },
  {
    path: "/tools/two-phase-flash",
    name: "TwoPhaseFlash",
    component: TwoPhaseFlash,
  },

  // New dynamic route for markdown blog posts
  {
    path: "/blog/:postId",
    name: "BlogPost",
    component: BlogPost,
    props: true,
  },
];

const router = new VueRouter({
  mode: "history",
  base: "/",
  routes,
});

export default router;
