<template>
  <v-container>
    <v-card-text>
      <v-row v-if="loading" justify="center" class="my-5">
        <v-progress-circular
          indeterminate
          color="primary"
        ></v-progress-circular>
      </v-row>

      <template v-else-if="error">
        <v-row justify="center" class="my-5">
          <v-alert type="error">
            {{ error }}
          </v-alert>
        </v-row>
      </template>

      <template v-else>
        <div class="blog-post-header">
          <h1 class="blog-post-title">{{ title }}</h1>
        </div>

        <v-row class="pl-2 pr-2 pb-3">
          <v-col cols="12" class="px-5">
            <markdown-renderer :content="content" />
          </v-col>
        </v-row>

        <div class="blog-post-navigation">
          <v-btn
            text
            color="#183153"
            class="blog-nav-btn prev-btn"
            :disabled="!prevPost"
            @click="navigateToPost(prevPost)"
          >
            <v-icon left size="18">mdi-arrow-left</v-icon>
            Previous Article
          </v-btn>

          <v-btn
            text
            color="#183153"
            class="blog-nav-btn next-btn"
            :disabled="!nextPost"
            @click="navigateToPost(nextPost)"
          >
            Next Article
            <v-icon right size="18">mdi-arrow-right</v-icon>
          </v-btn>
        </div>
      </template>
    </v-card-text>
  </v-container>
</template>

<script>
import MarkdownRenderer from "@/components/MarkdownRenderer.vue";
import { blogPosts } from "@/blogData.js";

export default {
  name: "BlogPost",
  components: {
    MarkdownRenderer,
  },
  props: {
    postId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      title: "",
      content: "",
      loading: true,
      error: null,
      nextPost: null,
      prevPost: null,
    };
  },
  created() {
    console.log("BlogPost created with postId:", this.postId);
    if (this.postId) {
      this.loadPost();
    }
  },
  mounted() {
    this.findAdjacentPosts();
  },
  watch: {
    postId() {
      this.loadPost();
      this.findAdjacentPosts();
    },
  },
  methods: {
    async loadPost() {
      this.loading = true;
      this.error = null;

      try {
        console.log("Loading post:", this.postId);

        // Import markdown file from src folder
        const markdown = await import(`@/blog-posts/${this.postId}.md`);
        let content = markdown.default; // Use default export from the loader

        console.log("Markdown loaded, length:", content.length);

        // Replace image paths that point to assets - this is the key fix
        content = content.replace(
          /!\[(.*?)\]\(\/assets\/(.*?)\)/g,
          "![$1](/assets/$2)"
        );

        // Extract title from the first line if it starts with #
        const lines = content.split("\n");
        if (lines[0].startsWith("# ")) {
          this.title = lines[0].substring(2).trim();
          this.content = lines.slice(1).join("\n");
          console.log("Title extracted:", this.title);
        } else {
          this.title = this.formatTitle(this.postId);
          this.content = content;
          console.log("Using formatted title:", this.title);
        }

        // Update page title
        document.title = `${this.title}`;
      } catch (error) {
        console.error("Error loading blog post:", error);
        this.error = `Could not load the blog post. Please try again later.`;
      } finally {
        this.loading = false;
      }
    },

    formatTitle(slug) {
      return slug
        .split("-")
        .map((word) => {
          // Special handling for CO2 or similar
          if (/co\d/.test(word.toLowerCase())) {
            return word.toLowerCase().replace(/co(\d)/, "CO$1");
          }
          return word.charAt(0).toUpperCase() + word.slice(1);
        })
        .join(" ");
    },

    findAdjacentPosts() {
      // Remove duplicate posts by route
      const uniquePosts = this.removeDuplicatePosts(blogPosts);

      // Import category order from blogData.js
      const { categoryOrder } = require("@/blogData.js");

      // Get current post slug from route
      const currentSlug = this.postId;

      // Group posts by category
      const postsByCategory = {};

      uniquePosts.forEach((post) => {
        if (!postsByCategory[post.category]) {
          postsByCategory[post.category] = [];
        }
        postsByCategory[post.category].push(post);
      });

      // Sort articles within each category by date
      Object.keys(postsByCategory).forEach((category) => {
        postsByCategory[category].sort(
          (a, b) => new Date(b.date) - new Date(a.date)
        );
      });

      // Find current post's category and index within category
      let currentCategory = null;
      let currentIndexInCategory = -1;

      // Find the current post's category and position
      Object.keys(postsByCategory).forEach((category) => {
        const posts = postsByCategory[category];
        const postIndex = posts.findIndex(
          (post) => this.getSlugFromRoute(post.route) === currentSlug
        );

        if (postIndex !== -1) {
          currentCategory = category;
          currentIndexInCategory = postIndex;
        }
      });

      if (!currentCategory) {
        console.warn("Current post not found in any category");
        this.nextPost = null;
        this.prevPost = null;
        return;
      }

      // Get proper category index from the ordered array
      const categoryIndex = categoryOrder.indexOf(currentCategory);

      console.log(
        "Current category:",
        currentCategory,
        "Category index in order:",
        categoryIndex,
        "Post index in category:",
        currentIndexInCategory
      );

      // If category not found in order, use a default high value to place it at the end
      const effectiveCategoryIndex = categoryIndex === -1 ? 999 : categoryIndex;

      // Determine previous post (earlier in sequence)
      if (currentIndexInCategory > 0) {
        // Previous post is the next post in same category (they're sorted newest first)
        this.prevPost =
          postsByCategory[currentCategory][currentIndexInCategory - 1];
      } else if (effectiveCategoryIndex > 0) {
        // Find the previous category that has posts
        let prevCategoryIndex = effectiveCategoryIndex - 1;
        while (prevCategoryIndex >= 0) {
          const prevCategory = categoryOrder[prevCategoryIndex];
          if (
            postsByCategory[prevCategory] &&
            postsByCategory[prevCategory].length > 0
          ) {
            // Previous post is the last post in previous category
            this.prevPost =
              postsByCategory[prevCategory][
                postsByCategory[prevCategory].length - 1
              ];
            break;
          }
          prevCategoryIndex--;
        }

        if (prevCategoryIndex < 0) {
          this.prevPost = null;
        }
      } else {
        // No previous post
        this.prevPost = null;
      }

      // Determine next post (later in sequence)
      if (
        currentIndexInCategory <
        postsByCategory[currentCategory].length - 1
      ) {
        // Next post is the previous post in same category (they're sorted newest first)
        this.nextPost =
          postsByCategory[currentCategory][currentIndexInCategory + 1];
      } else if (effectiveCategoryIndex < categoryOrder.length - 1) {
        // Find the next category that has posts
        let nextCategoryIndex = effectiveCategoryIndex + 1;
        while (nextCategoryIndex < categoryOrder.length) {
          const nextCategory = categoryOrder[nextCategoryIndex];
          if (
            postsByCategory[nextCategory] &&
            postsByCategory[nextCategory].length > 0
          ) {
            // Next post is the first post in next category
            this.nextPost = postsByCategory[nextCategory][0];
            break;
          }
          nextCategoryIndex++;
        }

        if (nextCategoryIndex >= categoryOrder.length) {
          this.nextPost = null;
        }
      } else {
        // No next post
        this.nextPost = null;
      }

      console.log("Previous post:", this.prevPost?.title);
      console.log("Next post:", this.nextPost?.title);
    },

    getSlugFromRoute(route) {
      // Extract the slug from a route path
      const parts = route.split("/");
      return parts[parts.length - 1];
    },

    navigateToPost(post) {
      if (!post) return;

      const slug = this.getSlugFromRoute(post.route);
      console.log("Navigating to:", slug);
      this.$router.push(`/blog/${slug}`);
    },

    removeDuplicatePosts(posts) {
      // Remove duplicate posts by route to ensure navigation works correctly
      const uniqueRoutes = new Set();
      return posts.filter((post) => {
        if (uniqueRoutes.has(post.route)) {
          return false;
        }
        uniqueRoutes.add(post.route);
        return true;
      });
    },
  },
};
</script>

<style scoped>
.blog-post-header {
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  position: relative;
}

.blog-post-header::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, #183153, #3a7ca5);
  border-radius: 3px;
}

.blog-post-title {
  color: #183153;
  font-size: 2.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  line-height: 1.3;
}

.blog-post-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem auto 1rem;
  padding: 0;
  max-width: 800px;
}

.blog-nav-btn {
  font-weight: 500;
  letter-spacing: 0.3px;
  text-transform: none;
}

.blog-nav-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.prev-btn {
  margin-right: auto;
}

.next-btn {
  margin-left: auto;
}

@media (max-width: 960px) {
  .blog-post-title {
    font-size: 1.8rem;
  }
}

@media (max-width: 768px) {
  .blog-post-navigation {
    padding: 0 1rem;
  }
}

@media (max-width: 600px) {
  .blog-post-header {
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
  }
}
</style>
